import { defineStore } from "pinia";
import {ref, watch} from 'vue'


export const useRatingStore = defineStore("ratingStore", () =>{
    let room_rating = ref([])

    const ratingLocal = localStorage.getItem("yourRating")
    if(ratingLocal){
        room_rating.value = ratingLocal.split(',')
        console.log(ratingLocal)
    }

    const getRating = async(id, rating) =>{

        room_rating.value[id] = rating
    }
    watch( () => room_rating, (member) => {
        localStorage.setItem("yourRating", member._rawValue)
        console.log(ratingLocal)
    }, {deep: true})
    return {
        room_rating,
        getRating
    }
})